body {
  background: #15151F;
  font-size: 16px;
}
.App {
  text-align: center;
}
.App-header {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
}
.title {
  font-size: 2rem;
  margin-bottom: 0;
}
.App-link {
  color: #61dafb;
}
.output {
  margin: 16px auto;
  display: none;
}
.output.show {
  display: block;
}
.output img {
  max-width: 480px;
}
