button {
    display: block;
}
.btn {
    background: #4A5EE5;
    border: none;
    border-radius: 24px;
    color: #FFF;
    cursor: pointer;
    margin-top: 16px;
    font-size: 1.2rem;
    padding: 12px 35px;
    transition: background 0.3s, transform 100ms ease-in-out;
}
.btn:hover {
    background: #4153cb;
}
.btn:active {
    transform: scale(0.9);
}